.Buttons{
    width: 100%;
    display: flex;
    justify-content: space-around;
}

.button {
    margin: 20px;
    height: 30px;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color .2s linear;
}

.button:hover{
    background-color: rgb(163, 163, 163);
}

.button:focus{
    outline: none;
}